import Vue from "vue";
import Vuex from "vuex";

import models from "./calculators";
import currencyList from "./currency";

Vue.use(Vuex);

const PREFERENCES_KEY = "PREFERENCES";

export default new Vuex.Store({
  state: {
    models,

    currency: { text: "US Dollar ($)", value: "US Dollar ($)", symbol: "$" },
    currencyList,

    darkMode: false,
  },

  getters: {
    preferences: function(state) {
      return { currency: state.currency, darkMode: state.darkMode };
    },

    list: function(state) {
      return { ...state.models };
    },

    calculatorWithName: function(state) {
      return (name) => {
        let calculator = state.models.general.find(
          (model) => model.name == name
        );
        if (!calculator) {
          calculator = state.models.business.find(
            (model) => model.name == name
          );
        }
        return JSON.stringify(calculator);
      };
    },

    currencyList: function(state) {
      return state.currencyList;
    },
  },

  mutations: {
    currency: function(state, currency) {
      state.currency = { ...currency };
    },

    theme: function(state, isDark) {
      state.darkMode = isDark;
    },

    loadPreferences: (state) => {
      const preferences = JSON.parse(
        window.localStorage.getItem(PREFERENCES_KEY)
      );

      if (preferences) {
        preferences.currency !== undefined
          ? (state.currency = preferences.currency)
          : "";
        preferences.darkMode !== undefined
          ? (state.darkMode = preferences.darkMode)
          : "";
      }
    },

    updatePreferences: (state) => {
      const preferences = JSON.stringify({
        currency: state.currency,
        darkMode: state.darkMode,
      });

      window.localStorage.setItem(PREFERENCES_KEY, preferences);
    },

    loadModels: function() {
      const savedInputs = Object.keys(window.localStorage).filter(
        (key) => key.includes("general") || key.includes("business")
      );

      savedInputs.map((id) => {
        this.commit("updateModel", id);
      });
    },

    updateModel: function(state, id) {
      id = id.split(",");
      const type = id[0];
      const calc = id[1];
      const inputs = "inputs";
      const index = id[2];

      const savedValue = window.localStorage.getItem(id);
      const loaded = savedValue === null ? false : true;

      state.models[type][calc][inputs][index]["loaded"] = loaded;
      state.models[type][calc][inputs][index].value = savedValue;
    },
  },

  actions: {},
});
